@import "../../styles/variables"

.button
  border: none
  outline: none
  background: transparent
  color: $grey
  font-size: 1.6rem
  border-bottom: .2rem dashed $grey
  margin: 2rem 0
  cursor: pointer

.content
  width: 100%
  border-radius: 1rem
  background: $grey-background
  margin-bottom: 5rem
  padding: 2rem
  display: grid
  grid-template-columns: 1fr 1fr 1fr
  grid-template-rows: max-content
  grid-auto-flow: row
  grid-gap: 1rem 3rem
  @media (max-width: 1000px)
    grid-template-columns: 1fr 1fr

.currency
  display: flex
  width: 100%
  justify-content: space-between
  align-self: center
  &__name
    font-size: 1.4rem
    color: $white
    font-weight: 400
  &__code
    font-size: 1.4rem
    color: $white
    font-weight: 700

.shown
  display: block

.none
  display: none