.button {
  border: none;
  outline: none;
  background: transparent;
  color: #989898;
  font-size: 1.6rem;
  border-bottom: 0.2rem dashed #989898;
  margin: 2rem 0;
  cursor: pointer;
}

.content {
  width: 100%;
  border-radius: 1rem;
  background: rgba(152, 152, 152, 0.5);
  margin-bottom: 5rem;
  padding: 2rem;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: max-content;
  grid-auto-flow: row;
  grid-gap: 1rem 3rem;
}
@media (max-width: 1000px) {
  .content {
    grid-template-columns: 1fr 1fr;
  }
}

.currency {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-self: center;
}
.currency__name {
  font-size: 1.4rem;
  color: #ffffff;
  font-weight: 400;
}
.currency__code {
  font-size: 1.4rem;
  color: #ffffff;
  font-weight: 700;
}

.shown {
  display: block;
}

.none {
  display: none;
}

